.primary-btn1 {
	border-radius: 5px;
	background-color: var(--primary-color1);
	font-family: var(--font-open-sans);
	font-weight: 600;
	font-size: 13px;
	color: #13141a;
	padding: 12px 20px;
	display: inline-flex;
	align-items: center;
	gap: 10px;
	transition: .5s;
	position: relative;
	overflow: hidden;
	z-index: 1;
	white-space: nowrap
}

.primary-btn1 svg {
	fill: var(--title-color);
	transition: .5s
}

.primary-btn1::after {
	position: absolute;
	content: "";
	display: block;
	left: 15%;
	right: -20%;
	top: -4%;
	height: 150%;
	width: 150%;
	bottom: 0;
	border-radius: 2px;
	background-color: #0a1019;
	transform: skewX(45deg) scale(0, 1);
	z-index: -1;
	transition: all .5s ease-out 0s
}

.primary-btn1:hover {
	color: var(--white-color)
}

.primary-btn1:hover svg {
	fill: var(--white-color)
}

.primary-btn1:hover::after {
	transform: skewX(45deg) scale(1, 1)
}

